import React, { Component } from "react";
import styled from "styled-components";

const Div = styled.div`
  text-align: center;
`;

const Circle = styled.div`
  width: 50px;
  height: 50px;
  border-width: 1px 1px 1px 0;
  border-style: bolde;
  border-radius: 50%;
`;

class Loading extends Component {
  render() {
    return (
      <Div>
        <Circle />
      </Div>
    );
  }
}

export default Loading;
